        text: "<div class='iframeArea'><iframe width='100%' height='100px' src='https://www.youtube.com/embed/vKFoGpFXBtk' frameborder='0' controls: 0; allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture' allowfullscreen></iframe></div>&nbsp;서기 2147년. 세계 4차 대전 발발. 세계는 둘로 나누어져 서로 싸우게 된다.\n&nbsp;2151년. 연합정부 대통령은 총 7개 도시에 핵폭탄을 투하할 것을 결정한다.\n&nbsp;2153년. 중앙군 총사령관은 그에 반발해 5개 도시에 핵폭탄을 투하할 것을 명령한다.\n&nbsp;끝없는 반복.\n&nbsp;\n&nbsp;그리고... 인류는 멸망했다.\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;멸망할 뻔했다. 비록 온 지상이 폭삭 내려앉고 바다까지 말라버렸지만 어쨌거나 아직은 숨이 붙어 있었다. 물론 사람들이 엄청나게 죽어 나가 바닥엔 시체가 굴러다녀 발에 차일 지경이기는 했다. 그래도 인류는 아직 살아 있었다. 우영도 살아 있었다.\n&nbsp;우영은 구도심의 빈민가에서 태어났다. 사실 구도심에 사는 이들은 모두 가난하기에 굳이 빈민가라고 지칭할 필요도 없었지만 그중에서도 유달리 가난한 동네는 있는 법이다. 우영은 할아버지와 둘이 살았다. 집이라고 하기에도 민망한 허술한 판잣집에서 잠을 잤다. 아버지는 누군지도 모르고 어머니는 돈을 벌러 갔다 소식이 없다고 했다. 그 동네 사람들은 대개 그런 사정을 갖고 있었으므로 우영은 개의치 않았다. 할아버지가 친할아버지는 맞는지가 좀 궁금했는데, 친손자가 아니라면 찢어지게 가난한 살림에 어린애를 기르지는 않을 것 같긴 했다.\n&nbsp;싸우기도 많이 싸웠지만 세상에 하나뿐인 가족이었던 할아버지는 우영이 커서 호강시켜 주기도 전에 사고로 죽었다. 아무래도 호강시켜주기는 그른 것 같으니 더 고생하지 않고 잘 된 거라고 생각하려 했지만 서러운 건 어쩔 수 없었다. 우영은 그때 열일곱 살이었다. 혼자 남은 우영은 먹고살기 위해 일해야 했다. 원래도 학교는 가본 적 없고 철든 뒤로는 할아버지 일을 도와 했었으므로 전과 그다지 달라지는 건 없었다.\n&nbsp;보물 사냥꾼. 이 낭만적인 명칭은 그 일을 하는 사람들이 스스로 붙인 것이다. 실상은 도굴꾼에 가깝지 않을까? 여러 번의 핵전쟁을 거쳐 옛 문명은 잿더미에 묻혀버렸다. 검게 굳은 땅을 파서 내려가다 보면, 지금의 구도심에서는 찾아볼 수 없는 신기한 물건들이 나오곤 했다. 그게 바로 '보물'이다. 신도심의 부자들은 기꺼이 큰돈을 내고 보물을 사 갔다. 언젠가 눈이 튀어나올 정도로 비싼 보물을 발견하는 것이 우영의 꿈이었다.\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;/\n&nbsp;\n&nbsp;그날도 우영은 무너진 건물 안을 뒤지고 있었다. 이 동네는 이백 년 전에는 꽤 번화한 동네였던 듯싶었다. 그러나 그때 지어졌던 멋진 빌딩과 근사한 인테리어는 이미 흔적을 발견하기 어려운 상태로 부서져 있었다. 집이었다면 2층 이상의 대저택이었을 어떤 무더기를 우영은 삽으로 파내고 또 파냈다. 조금이라도 값이 나갈 만한 것은 수레에 실은 상자로 던져 넣었지만 과연 가격이 얼마일지는 의문이었다. \n&nbsp;날은 아주 맑았고 햇빛이 쨍쨍했다. 그러나 눈에 보이는 건 폐허가 된 도시뿐이었고 사람은 아무도 없었다. 우영은 오늘 여기 오는 데도 2시간을 혼자 수레를 끌고 걸어와야 했다. 지금은 인구가 아주 적었고, 그나마도 사람들은 전부 도심에 모여 살았다. 도심을 벗어나면 그 밖에는 우영 같은 보물 사냥꾼 말고는 코빼기도 보기 힘들었다. 그러니 다른 사람을 만날 거라는 상상도 하지 못한 채 우영은 아무 생각없이 삽집을 했다. 다시 땅에 삽을 박아 넣는데, 뭔가 단단한 게 닿았다. 크기도 꽤 큰 것 같았다. 우영은 당장 삽을 집어던지고 손으로 흙더미를 헤집었다. 제발 이게 세탁기이길. 옷을 빨아주는 요사스러운 물건이 잘 팔린다는 말을 들었던 것이다. \n&nbsp;나온 것은 사람의 얼굴이었다. \n&nbsp;으악! 우영은 외마디 비명을 지르며 뒤로 물러났다. 그러나 쿵쾅대는 심장을 진정시키고 생각해 보니 백 년도 더 전에 핵폭탄을 맞고 무너진 건물 밑에서 사람이 살아있을 리는 만무하고 시체마저도 멀쩡할 리가 없었다. 정교하게 만들어진 인형 같은 것이라 생각하니 맥이 탁 풀렸다. 이렇게 공들여 만든 인형이라면 장난감이라도 비싸게 팔리지 않을까? 우영은 다시 가까이 다가가 잠든 듯 눈을 감은 얼굴을 자세히 살펴보았다.\n&nbsp;그는 금발이었고 남자의 얼굴을 하고 있었다. 표정이 없었는데도 굳게 다물린 입매의 양 끝은 새침하게 올라온 모양이었다. 그리고 점이 있었다. 뺨에도, 닫힌 눈꺼풀에도. 마치 진짜 같았다. 아무리 세심한 공예가라도 이런 데까지 점을 찍겠냐며. 우영은 손을 뻗어 콧대와 눈꺼풀 사이의 점을 문질러보았다. 지워지지 않는 걸 보니 오물이 묻은 것은 아니었다. \n&nbsp;우영은 머리카락도 헤집어보고, 귓바퀴도 주물러보고, 아직 반쯤 파묻혀있는 목도 쓰다듬어봤다. 모든 게 진짜 사람 같았다. 숨결과 맥박이 느껴지지 않는 것만 빼면. 붉은 입술도 부드럽고 말랑했다. 문득 머릿속에 뭔가 떠올라 억지로 손가락을 집어넣어 입을 벌렸다. 물컹한 혀가 느껴졌다. 딱딱한 이도 느껴졌다. 앞니와 송곳니, 어금니까지 전부 있었다. 우영은 정교함에 감탄하며 가장 깊숙이 있는 어금니까지 손으로 훑었다. \n&nbsp;그때였다. 그가 눈을 번쩍 떴다. \n&nbsp;처음엔 그렇게까지 놀랍지 않았다. 왜, 어린애들이 갖고 노는 인형 중에는 눈을 감았다 떴다 하는 것도 있으니까. 이렇게 리얼하게 만든 인형이라면 그 정도 기능쯤은 있을 테고, 낡았으니 아무 일 없이 눈을 감았다 떴다 하는 것도 있을 법했다. 하지만 잠시 후, 우영은 소스라치게 놀라 귀를 의심해야 했다.\n&nbsp;\n&nbsp;“나 좀 꺼내줘.”\n&nbsp;\n&nbsp;으아악! 우영이 아까보다 더 크게 소리를 지르며 입에서 손을 빼냈다. \n&nbsp;\n&nbsp;“빨리.”\n&nbsp;\n&nbsp;그는 우영을 똑바로 쳐다보며 다시 한 번 말했다. 잘못 들은 게 아니었다. 잘못 본 것도 아니었다. 두 눈을 생기를 띠고 있었고 목소리도 그 입에서 흘러나온 게 분명했다. 마치 살아있는 인간 같았다. 우영은 다리에 힘이 풀려 바닥에 주저앉았다. 이 남자는 그 오랜 세월 동안 땅 밑에 갇힌 채 대체 어떻게 살아남은 걸까?\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;<b>이터니티</b>\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;묻혀있던 몸을 마저 꺼내주면서, 옷은 입었지만(심지어 이 옷은 썩지도 않았다) 신발은 안 신은 그 남자를 수레에 태워 데려가면서 이것저것 물어봤지만 그는 아는 게 하나도 없었다. 이름도 나이도, 왜 땅에 묻혀 있었는지도. 사람을 파낼 생각은 없었는데, 그렇게 됐다. 폐허에서 구해낸 남자를 다시 버려두고 갈 수도 없는 노릇이라 우영은 일단 그를 닥터에게 데려가기로 했다.\n&nbsp;\n&nbsp;“넌 이름이 뭐야?”\n&nbsp;“난 정우영.”\n&nbsp;“우영이?”\n&nbsp;“그래.”\n&nbsp;\n&nbsp;건방지게도 생명의 은인에게 반말을 찍찍한다. 핵전쟁 전에 묻혔다면 우영보다 나이가 훨씬 많겠지만 액면가는 또래로 보였다. 애초에 인간이 그 상황에서 살아남을 수 있느냐부터 의문이었지만. 우영은 귀신을 만난 것은 아닌지 등이 오싹해졌다. 하지만 귀신이라기엔 지나치게 밝아 보였다. 신발도 안 신은 사람을 걷게 할 수는 없어서 수레에 앉히고 우영이 수레를 끌고 걸었다. 주위에는 온통 무너진 건물밖에 없었는데도 그는 신기한 듯이 주위를 두리번거리며 구경했다. 마치 천진난만한 어린아이 같았다.\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;/\n&nbsp;\n&nbsp;닥터는 사실 닥터가 아니다. 구도심에 대학 따윈 없으니 당연히 학위도 땄을 리가 없다. 아무 대학도 다닌 적이 없으니 의사도 아니다. 닥터를 닥터라고 하는 건 똘똘한 아이더러 '척척박사'라고 하는 것이나 마찬가지다. 사실 닥터는 구도심에서 가장 큰 고물상의 감정사다. 온갖 보물 사냥꾼들이 가게를 드나들며 닥터에게 퇴짜를 맞거나 오케이를 받는다. 구도심과 신도심은 19세기와 21세기만큼 차이가 컸기 때문에 보물 사냥꾼들도 정작 그 물건들이 정확히 어떤 쓰임이 있는 건지는 몰랐다. 그래서 '보물'은 찾으면 먼저 감정을 받아야 했다. 아주 작은 광석에서부터 커다란 고철에 이르기까지 닥터는 모르는 것이 없다. 어린 시절, 마찬가지로 보물 사냥꾼이었던 할아버지의 일을 돕던 때부터 닥터의 가게에 발을 들인 우영은 전적으로 닥터를 신용하고 있었다. 닥터라면 수십 년 전 생매장 당하고도 살아난 기억상실증 남자의 정체를 밝혀줄지도 모른다.\n&nbsp;\n&nbsp;“안드로이드네.”\n&nbsp;“네?”\n&nbsp;\n&nbsp;닥터의 고물상은 구도심과 신도심의 경계지에 위치해 있다. 정확히 말하면 반 갈라져서 구도심에 닥터의 감정실이 있고 신도심에 가게가 있다. 가게에는 미남 사장과 미남 알바생이 있고, 우영이 감정을 기다리는 동안이면 둘 중 한 명이 간식을 주는 것도 이 집 단골이 된 이유라고 할 수 있겠다. 우영이 닥터에게 남자를 보여주자 닥터는 그를 이리저리 살펴보더니 마치 수술대 같이 생긴 딱딱한 침대에 눕힌 후 얼굴을 빼고는 암녹색의 두꺼운 천으로 가렸다. 그 모습이 정말 수술 받는 환자 같았다. 천장에 커튼처럼 천을 달아 놓고 그 안에서 닥터는 뭘 하는지 모르겠다. 뭔가가 달그락거리며 부딪히는 소리는 마치 외과 의사가 개복수술이라도 하는 듯했다. 천 밑으로 보이는 남자의 얼굴은 그저 평온했다. 그동안 우영은 미남 알바생이 갖다 준 시원한 차와 달지 않고 딱딱한 과자를 먹었다.\n&nbsp;\n&nbsp;“그거 오래전에 멸종됐잖아요.”\n&nbsp;\n&nbsp;기나긴 수술 같은 감정을 마친 닥터의 말이 황당해서 우영은 과자를 씹다 말고 소리쳤다. 안드로이드라는, 마치 인간 같은 로봇이 존재했었다는 것은 알고 있었다. 그러나 한때 인간보다 더 우월한 위치를 점하기도 했던 안드로이드들은 제6차 산업혁명이 발발하며 그 씨가 말라버렸다. 이제는 신도심에서조차 그 모습을 찾기 어렵다고 했다. \n&nbsp;\n&nbsp;“K사 초기 모델인가 봐. K사 부품이 몇 개 있네. 근데 카탈로그에 없는 모델명이야. 시험작인가?”\n&nbsp;\n&nbsp;닥터가 남자의 발치에서 우영에게 와보라며 손짓했다. 우영이 몸을 일으켜 그쪽으로 가자 닥터가 천을 들어 올려 남자의 무릎 위 허벅지에 일렬로 새겨진 글씨를 보여주었다. SAN-07010. 하지만 우영은 닥터처럼 K사 카탈로그를 달달 외우고 있는 사람이 아니었다. 에스에이엔? 산? 어떻게 읽는지조차 알 수 없었다.\n&nbsp;조금만 시선을 위로 올리자 닥터가 정말로 남자의 배를 연 것을 알 수 있었다. 천에 가려 다 보이지는 않았지만 마치 피부 같은 표면을 가르고 그 사이로 은회색 기기와 원색의 배선들이 보였다. 무너진 폐허에서 파냈을 때부터 범상치 않다고 느끼긴 했지만 막상 진짜로 신체 내부가 기계로 채워진 것을 보자 기분이 꺼림칙해졌다. 우영은 다시 자리로 돌아가 앉았다.\n&nbsp;\n&nbsp;“그래서 얼마예요?”\n&nbsp;“팔면 진짜 비싸게 팔 수 있을 거 같아.”\n&nbsp;\n&nbsp;솔직하게 대답하는 닥터의 말에 우영은 눈이 번쩍 뜨였다.\n&nbsp;\n&nbsp;“이제 정말 세상에 없는 귀한 거잖아. 오래됐는데 상태도 좋은 것 같네. 안드로이드는 안 그래도 비싼데 초기 시험작이면 더 비쌀 거 같은데?”\n&nbsp;“그래서 얼마냐고요.”\n&nbsp;\n&nbsp;닥터는 오래 고민하지 않고 계산기에 숫자를 찍어주었다. 계산기를 건네받은 우영은 찍힌 숫자를 몇 번이고 읽고 또 읽었다. 우영은 평생 이 돈을 벌 수조차 없을 것 같은데 신도심에는 정말 이 돈을 내고 물건을 사는 사람도 있는 것인가? 우영이 당장 팔겠다고 말하려고 입을 여는 순간, 안드로이드가 손을 뻗어 우영의 팔목을 덥석 잡았다.\n&nbsp;\n&nbsp;“우영아. 나 팔지 마.”\n&nbsp;\n&nbsp;언제 봤다고 친한 척 이름을 부른다. 우영은 손을 뿌리치려 했지만 안드로이드는 꽉 잡고 놓아주지 않았다. \n&nbsp;\n&nbsp;“나 변신도 할 수 있고 레이저도 쏠 수 있어. 너 일하는 거 도와줄게. 장기적으로 봤을 때 그게 더 좋을 거야. 그러니까 나 팔지 마.”\n&nbsp;\n&nbsp;수술대 같은 침대에 거꾸로 누워 있는 주제에 말은 술술 나왔다. 우영은 다시 계산기에 찍힌 숫자를 내려다보았다. 그러나 쉽게 포기하기는 어려운 금액이었다. 우영은 망설였다. 저 녀석이 말하는 감언이설에 바로 넘어간 것은 아니었다. 아직도 잡혀있는 손목 때문이었다. \n&nbsp;손목을 잡은 손바닥이 뜨끈뜨끈했다. 감히 쉽게 뿌리치지 못하게 꽉 잡은 악력도 그랬다. 이 모든 게 만들어진 것이란 게 믿어지질 않았다. 우영은 머릿속이 혼란해졌다. 힐끗 시선을 돌려 얼굴을 보자 두 눈이 초롱초롱했다. 단호하게 다문 입술은 절대로 고물상에 팔리지 않겠다는 의지가 느껴졌다. 우영은 분명 아까 저 녀석의 뱃속에 내장과 혈관 대신 기기와 배선이 들어있던 걸 봤는데도 이 가짜 남자가 인간인 듯한 착각이 일었다. 우영은 다시 한 번 계산기를 들여다보았다. 흙더미에서 파낸 낡은 기계라면 얼마에 팔아도 좋았다. 하지만 사람이라면 얘기가 달라진다. 물론 이 남자는 사람이 아니라고 하지만. 그런데 속에 피와 살이 없다는 것만 빼면 사람과 다를 게 없는데 마냥 아니라고 하기가 어려웠다. 만약 이대로 이 남자를 팔면 인신매매를 한 듯한 죄책감에 시달릴 것 같았다.\n&nbsp;우영이 닥터에게 물었다.\n&nbsp;\n&nbsp;“안 팔아도 괜찮아요?”\n&nbsp;“안 괜찮을 건 뭐 있어? 내가 인센티브 받는 것도 아니고 월급 받는 처지에.”\n&nbsp;\n&nbsp;닥터는 무심하게 천을 걷어내며 말했다. 어느새 남자의 배는 말끔하게 봉합되어 있었다. \n&nbsp;언제 열었는지조차 모르게 깔끔하게도 닫힌 상체는 근사한 근육이 붙은 몸이었다.\n&nbsp;\n&nbsp;“그치만......”\n&nbsp;“사장만 모르면 되는 거 아냐?”\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;우영은 닥터에게 계산기를 돌려주었다. 닥터는 계산기를 받아 버튼을 눌러 숫자를 0으로 만들었다. 그걸 보자 가슴이 좀 아파지려고 했다. 하지만 아마, 아직도 손목을 잡고 있는 안드로이드를 팔았다면 더 가슴이 아팠을 거다. 팔려나가지 않은 남자는 싱글벙글 웃고 있었다. 웃으니까 뺨에 깊은 보조개가 파였다. 이렇게까지 정교하고 인간적인 인형을 만들 수 있다니 과거의 기술력은 참으로 대단한 것이었다.\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;/\n&nbsp;\n&nbsp;우영은 다시 안드로이드를 앉힌 수레를 끌고 이번엔 집으로 왔다. 맨발의 남자가 집에 들어선 순간, 뭔가 깨달은 게 있었다. 할아버지가 죽은 후로 이 집에 누군가 들어온 건 처음이었다. 물론 이 남자는 사람은 아니지만. 사람과 너무 비슷해서 자꾸 착각을 했다.\n&nbsp;그는 좁은 집 한가운데에 서서 주위를 한 바퀴 둘러보았다. 너무 좁아서 시간도 얼마 걸리지 않았다. 문득 그가 벽에 붙은 사진을 가리키며 물었다.\n&nbsp;\n&nbsp;“이 사람은 누구야?”\n&nbsp;“할아버지야.”\n&nbsp;“우영이 할아버지? 닮았네.”\n&nbsp;“그런 말 많이 들었어.”\n&nbsp;\n&nbsp;남자는 사진 앞으로 걸어가서 얼굴을 가까이 대고 빤히 바라보기까지 했다. 그 사진은 우영의 8살 생일에 찍은 것으로, 고물상의 미남 사장이 가게에 진열해둔 폴라로이드 카메라로 선심 써서 한 장 찍어준 것이다. 사진 밑에는 할아버지가 날짜와 함께 ‘사랑하는 영이 생일 축하’라고 써두었다. 남자는 조심스레 어린 우영의 얼굴을 매만졌다.\n&nbsp;\n&nbsp;“우영이 애기 때 귀여워. 근데 우영이 생일이 11월 26일이야?”\n&nbsp;“응.”\n&nbsp;“그럼 내 생일은 오늘로 할까? 난 생일 모르니까. 오늘 며칠이야?”\n&nbsp;“9월 2일. 근데 넌 생일도 몰라?”\n&nbsp;“난 이름도 모르잖아.”\n&nbsp;\n&nbsp;남자는 천진난만한 얼굴로 그렇게 대답했다. 그러고 보니 이름도 모르는데 생일은 알고 있을 리 만무했다. 졸지에 이름에 더불어 생일까지 지어주게 생긴 우영은 머리를 싸맸다. 생일은 대충 오늘로 한다 쳐도 이름은 중요했다. 할아버지도 우영의 이름을 공들여 지었다고 했었다. 아무 이름이나 붙여주긴 싫었다. 멀뚱멀뚱 서서 쳐다보는 애는 아무 아이디어도 없어 보였다. 불현듯 어떤 발상이 우영의 머리를 스치고 지나갔다.\n&nbsp;\n&nbsp;“야. 너 다리에 시리얼 넘버 있잖아.”\n&nbsp;“응?”\n&nbsp;“그거 좀 다시 보여줘 봐.”\n&nbsp;\n&nbsp;그는 아무렇지도 않게 바지를 쑥 내렸다. 우영은 눈살을 찌푸렸다. 이 로봇에게는 인간의 매너를 좀 가르쳐줄 필요가 있는 것 같다. 다행히 속옷은 입고 있었지만. 어쨌든 드러난 허벅지에는 일련번호가 적혀 있었다. SAN-07010.\n&nbsp;\n&nbsp;“이거 네 이름이랑 생일 아니야?”\n&nbsp;“나야 모르지.”\n&nbsp;\n&nbsp;우영은 손가락으로 번호를 쓸어보았다. 제품에 새겨놓은 일련번호라기보다는 피부의 타투 같은 느낌이 들었다. 하지만 이 안드로이드가 너무 정교하게 만들어져서 그런 것일 뿐일지도 몰랐다. 아무튼 간에 우영은 이미 이 영문자와 숫자에서 따와 이름과 생일을 정해주기로 결심했다.\n&nbsp;\n&nbsp;“이제 네 이름은 산이고 생일은 7월 10일이다.”\n&nbsp;“너무 대충이잖아.”\n&nbsp;\n&nbsp;산은 부루퉁하게 입술을 내밀었다. \n&nbsp;\n&nbsp;“그렇다고 내 맘대로 붙이는 것도 좀 그렇잖아. 다 이유가 있으니까 새겨놓은 걸 텐데.”\n&nbsp;“내 생일이 되려면 거의 1년을 기다려야 되네.”\n&nbsp;“그게 불만이었냐?”\n&nbsp;\n&nbsp;어처구니없어하는 우영을 뒤로 하고 산은 우영과 할아버지의 사진 옆에 걸린 달력을 넘겨보았다. \n&nbsp;\n&nbsp;“지금은 몇 년이야?”\n&nbsp;“2222년.”\n&nbsp;“우영이는 몇 년에 태어났어?”\n&nbsp;“99년.”\n&nbsp;“1999년?”\n&nbsp;“뭔 소리야. 당연히 2099년이지.”\n&nbsp;\n&nbsp;산은 잘 이해가 가지 않는다는 표정이었다. 이 안드로이드의 입장에서는 잠들어 있다가 깨어나 보니 백년쯤 흘러있는 셈이니 이해 못할 것도 아니었지만. 그렇다고 해도 우영을 1999년생으로 착각하는 건 어이가 없었다. 우영이 알기로 그 시절에는 그럴듯한 안드로이드를 만들 기술력이 없었다고 하니 산도 훨씬 나중에 만들어졌을 것이다.\n&nbsp;\n&nbsp;“넌 잠 안 자도 되지?”\n&nbsp;\n&nbsp;할아버지가 돌아가신 뒤로 혼자 지내온 우영의 집에는 한 명 분의 가재도구밖에 없었다. 산이 사람이었다면 이것저것 마련해야 해서 곤란했을 테지만, 다행히도 안드로이드인 탓에 아무것도 필요 없을 것 같아 우영은 내심 안심이었다. 그러나 산은 단호한 태도로 잠자리가 있어야 한다고 우겼다.\n&nbsp;\n&nbsp;“안드로이드도 잠자.”\n&nbsp;“웃기지 마. 잠을 왜 자?”\n&nbsp;“충전은 해야 할 거 아냐.”\n&nbsp;“앉아서 하면 되잖아.”\n&nbsp;“제조사는 안드로이드를 누운 자세로 충전하도록 권장하고 있어.”\n&nbsp;\n&nbsp;이렇게 나오니 우영도 할 말이 없어졌다. 뭐, 어차피 로봇이다. 이불 없이 잔다고 허리가 배기는 것도 아니고, 바닥에 누울 자리만 만들어주면 될 거라고 생각했다. 그러나 산은 예상외로 까다로운 안드로이드였다.\n&nbsp;\n&nbsp;“이불도 없이 어떻게 자라고?”\n&nbsp;“넌 그냥 자도 되잖아. 여분 이불이 없어.”\n&nbsp;“그럼 우리 둘이 침대에서 자면 되지.”\n&nbsp;“뭐? 야, 침대를 봐. 저 좁은 데서 둘이 어떻게 자냐?”\n&nbsp;\n&nbsp;오래전 할아버지가 주워 온 목제 프레임을 수선해 그 위에 이불만 올려서 만든 침대는 아주 작았다. 체구가 그다지 크지 않은 우영도 작다고 느낄 정도니 말 다 했다. 도저히 남자 둘이 누워 잘 크기가 아니었는데도, 산은 반드시 침대에서 자야겠다고 박박 우겼다. 결국 두 손 두 발 다 든 우영은 이미 누워서 충전 모드에 들어간 산의 옆에 몸을 낑겨 넣었다. \n&nbsp;그러고 보니 누군가와 함께 자는 건 오랜만이었다. 인간의 온기 같은 건 느껴지지 않지만. 정확히 말하면 온기는 느껴졌지만, 그것도 정교하게 만들어진 가짜였다. 우영은 외로움이 조금은 달래졌는지, 아니면 더 외로워졌는지 잘 분간이 가질 않았다. 어쨌거나 침대가 너무 좁았다. 내일 침대로 쓸 만한 소재가 있으면 주워 와야겠다. 그런 생각을 하다 보니 어느새 잠이 들고 말았다.\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;/\n&nbsp;\n&nbsp;눈을 떴을 때는 몸 위로 뭔가 무거운 게 올라와 있었다. 잠이 덜 깨 한동안은 그게 뭔지도 몰랐다가, 정신을 차려보니 그건 산의 팔이었다. 이 녀석은 안드로이드 주제에 잠버릇까지 있었다. 우영은 몸을 일으키며 팔을 치워냈다. 철로 만들어진 팔 치고는 가벼웠다. 그냥 딱 근육질 남자 팔 같은 무게감이었다.\n&nbsp;해 뜨기 직전의 시간이었다. 구도심 사람들은 하루가 빠르다. 신도심만큼 전기를 마음껏 쓸 수 없기 때문이다. 우영은 아직도 침대에 누워있는 산을 깨우고 나갈 채비를 했다. 둘이 집을 나섰을 때는 막 해가 뜨기 시작해서 세상이 환해질 시점이었다. 둘은 어제 우영이 산을 파냈던 현장으로 되돌아갔다. 어제는 사람 같은 안드로이드를 발견한 탓에 그 후로 아무것도 못 했다. 산이 묻혀있었던 걸 보면 세계가 멸망하기 전에는 꽤 잘 살던 동네였던지도 모른다. 우영은 내심 또 귀한 것이 묻혀있기를 바랐다.\n&nbsp;한 남자와 한 남성형 안드로이드는 열심히 땅을 팠다. 산에게는 할아버지가 생전에 쓰던 작업복과 도구를 빌려주었다. 날이 너무 쨍쨍해서 땀이 줄줄 흘렀다. 우영은 허리를 펴며 이마에 흐르는 땀을 닦아냈다. 오전 내내 작업을 했지만 돈 될 만한 것은 없었다. 산처럼 정교하고 섬세하게 만들어진 안드로이드라면 분명 그 시대에도 천문학적인 금액으로 거래되었을 텐데, 그렇다면 산은 굉장한 부잣집에서 살았을 게 틀림없었다. 그러니 세상이 멸망하는 순간 산이 있었던 곳은 호화로운 저택이거나 거대한 빌딩, 뭐 그런 것이었어야 하는 것 아닌가. 산만큼 비싼 물건도 많았을 테고. 그러나 아무리 헤집어도 그럴듯한 것은 나오지 않았다. 산은 혼자 등산을 갔다가 산사태가 나서 파묻힌 것이 아닌가 하는 의심마저 들었다.\n&nbsp;\n&nbsp;“근데 넌 땀이 안 나네?”\n&nbsp;\n&nbsp;문득 산을 쳐다본 우영이 물었다. 땀으로 푹 젖은 우영과 달리 산은 흙이며 검댕이 묻은 걸 빼면 아침과 다름없이 뽀송했다. 산은 뭘 새삼스럽게 그런 걸 묻느냐는 듯이 태연하게 대답했다.\n&nbsp;\n&nbsp;“난 사람이 아니니까.”\n&nbsp;\n&nbsp;안드로이드니까 땀이 안 난다, 는 것까지는 납득할 수 있다. 그런데 이렇게까지 사람처럼 만들어놓고 사실은 사람이 아니니까 땀이 안 나요, 는 잘 이해가 가질 않았다. 그럴 거면 좀 덜 사람 같이 만들든가. 이렇게 진짜 같이 만들 거면 그냥 땀도 나게 만들든가. 뭔가 애매하게 느껴졌다.\n&nbsp;산은 땀만 안 나는 게 아니라 피로도 쌓이지 않는 것 같았다. 작업 속도가 현저히 느려진 우영과 달리 산은 여전히 움직임이 스무스했다. 하지만 역시 좀 애매한 것이, 안드로이드라고 해서 힘이 세거나 특이한 기술을 갖고 있는 건 아니었다. 그저 근육이 좀 있는 젊은 남자다운 근력과 솜씨였다. 당연히 변신과 레이저는 뻥이었다. 괘씸해서 한 대 때려주려다 그래도 손이 한 명 더 있는 게 도움이 되긴 하니까 참았다. \n&nbsp;\n&nbsp;“여기는 원래 어디였을까? 너희 집?”\n&nbsp;“난 기억이 안 나.”\n&nbsp;\n&nbsp;산은 바로 대답을 해놓고도 주위를 한 번 둘러보았다. 모든 건물이 주저앉아 지평선이 보일 정도로 허허벌판이었다.\n&nbsp;\n&nbsp;“근데 여기는 아니었던 거 같아.”\n&nbsp;“넌 엄청 부잣집에서 살았겠지? 안드로이드는 비싸니까?”\n&nbsp;“모르겠어.”\n&nbsp;“말을 말자.”\n&nbsp;\n&nbsp;우영은 그냥 대화를 포기하기로 했다. 한국말을 할 줄 안다는 것이 기적일 정도로, 산은 아는 게 하나도 없었다. 그렇게 오래 잠들어 있었는데 말이라도 기억한다는 게 기특한 수준이었다. 그래도 안드로이드라면 분명 어떤 분야에는 특화되어 있을 텐데. 일단 힘쓰는 노동은 아닌 것 같았다. 관상용이라면 어떨까? 우영은 눈을 가늘게 뜨고 산을 쳐다보았다. 옛 신화에 나올 법한 천 쪼가리를 몸에 두르고 조각처럼 포즈를 취한 산을 상상하자 웃음이 터졌다.\n&nbsp;\n&nbsp;“왜?”\n&nbsp;\n&nbsp;산이 어리둥절하게 물었다. 우영은 웃음을 참으며 아무것도 아니라고 둘러댔다. 그러다 문득 생각이 나서 덧붙였다.\n&nbsp;\n&nbsp;“너 혹시 요리나 청소 같은 거 잘 하나?”\n&nbsp;\n&nbsp;안드로이드는 뭔가 용도가 있었음이 틀림없으니, 어쩌면 산은 그 저택에서 요리나 청소를 담당했을지도 모른다. 보물 사냥에는 그다지 재능이 없어도 요리나 청소에는 특화되어있을 수도 있다. 집에 가서 한 번 시켜봐야겠다. 우영은 속으로 결심했다.\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;/\n&nbsp;\n&nbsp;이름만 보물 사냥꾼이지 보물을 구경도 못해보는 나날이 흘러갔다. 우영의 짧지 않은 경력 중에서 가장 비싸고 귀한 것이 산이었는데, 정작 산은 어디에 써먹는 건지 도통 알 수가 없었다. 그래도 없는 것보다는 나았다. 수레도 끌어주고, 땀도 닦아주고, 우영이 심심할까봐 그런다는 핑계를 대고 끊임없이 조잘거리기도 했다.\n&nbsp;요리도 못하고 청소도 못하고 삽질에도 소질이 없는 산은 말은 참 많았다. 아는 것도 없으면서 어떻게 말은 그렇게 많은지 의문이었다. 계속 떠들고 싶어하지만 알고 있는 화제가 별로 없었으므로 산과 우영의 대화는 대개 산의 질문과 우영의 대답으로 이루어져 있었다. 귀찮은 녀석이라고 생각하면서도 우영은 꼬박꼬박 산의 물음에 답을 해주었다. 아는 게 없는 만큼 산은 알고 싶어하는 것도 많았다. 왜 구도심에는 제대로 된 건물이 하나도 없는지. 왜 도시가 폐허가 되었는지. 신도심은 얼마나 삐까번쩍한지. 사진 속 할아버지는 지금 어디 계신지도.\n&nbsp;\n&nbsp;“돌아가셨어.”\n&nbsp;“그런 거야? 명복을 빕니다.”\n&nbsp;\n&nbsp;고개를 숙이고 묵념하는 산의 모습에 우영은 저항 없이 웃음을 터트렸다. 자기 이름조차 몰랐으면서 이런 건 어디서 배웠는지 모르겠다. 마치 자식이 성장하는 과정을 지켜보는 부모의 마음이 이럴 것 같았다. 산을 데리고 온 후로 집은 좁아졌고 일에 도움이 되지도 않았지만 솔직히 말해 우영은 좀 즐거워졌다. 이렇게 활기를 띤 생활은 몇 년 만에 처음이었다. 둘은 매일같이 함께 일어나 일을 나갔고, 이런저런 대화를 나누며 함께 일을 했다. 몇 시간에 걸친 작업을 끝난 후에는 함께 고물상에 갔고 감정을 기다리는 동안에는 둘이 나란히 앉아 미남 알바생이 가져다준 간식을 먹었다. 감정이 끝난 후에는 돈을 받아 집으로 가는 길에 식재료를 사서 저녁을 해 먹고 해가 지면 이른 시간에 비좁은 침대에 함께 누워 잤다. 이런 나날을 보내는 동안 날이 좀 더 추워졌다. 우영의 옷이 두꺼워졌다. 산은 추위를 느끼지 않는다고 했지만 얇게 입은 꼴을 보기 싫어 할아버지의 겨울옷을 꺼내주었다. 아침에 일 나가기 전 목도리를 둘러주면 산은 바보같이 헤 웃었다.\n&nbsp;\n&nbsp;“둘이 잘 지내고 있나 봐.”\n&nbsp;\n&nbsp;어느 날 닥터가 그렇게 말했다. 우영과 산은 감정실의 기다란 대기석에 나란히 앉아 미남 사장이 끓여준 따뜻한 차를 마시던 중이었다. 그날의 전리품인 뭔지 모를 장난감 같은 것들을 닥터가 살펴보는 사이 둘은 어떤 게 제일 비쌀지 내기하자며 투닥거리고 있었다. 뜬금없이 던진 닥터의 말에 우영은 황당하다는 듯 네? 하고 되물었다.\n&nbsp;\n&nbsp;“그때 가져가라고 하길 잘한 것 같아. 할아버지 돌아가신 뒤로 기운이 없었잖아.”\n&nbsp;“......할아버지 돌아가신 게 몇 년 전인데요.”\n&nbsp;“그러니까 그만큼 오랫동안 기운이 없었다는 거지.”\n&nbsp;“그런 걸 신경 쓰고 있었어요?”\n&nbsp;“우영아, 내가 널 본 지가 몇 년인데.”\n&nbsp;\n&nbsp;당연히 걱정되지. 닥터가 다정한 미소를 지으며 말했다. 우영은 옆에서 산의 시선이 느껴져서 왠지 부끄러웠다. 차마 고개를 돌릴 수 없었다.\n&nbsp;\n&nbsp;“나 잘 데리고 온 거 같아?”\n&nbsp;\n&nbsp;그렇게 말하는 산의 목소리에는 기쁨이 담겨 있었다. 우영은 인정하기가 싫어서 쳐다도 안 보고 옆구리를 퍽 쳤다. 아프다고 찡찡대는 목소리도 왠지 기쁜 것 같았다. 사람도 아니면서 이런 감정을 느끼다니 이상했다. 사람도 아닌 것에 이런 안정감을 얻는 우영도 이상한 것 같았다.\n&nbsp;날이 더 추워졌다. 해가 짧아져서 일할 수 있는 시간이 줄어들었다. 아침에는 너무 추워 일어나기가 싫어서 서로를 안고 한참을 꾸물대다가 일어났다. 그렇게 간신히 일을 나가도 소득은 별로 없었다. 기온이 낮아져 땅이 딱딱해진 탓에 발굴 작업이 어려웠다. 고물상에 들르지 못하고 바로 집으로 돌아가는 나날이 이어졌다. 산은 여전히 아무 생각 없이 조잘댔지만 우영은 하루 벌어 하루 먹고 사는 처지가 걱정이었다.\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;/\n&nbsp;\n&nbsp;“영아, 여기 봐봐.”\n&nbsp;\n&nbsp;빈손으로 집에 가는 길이었다. 우영은 산이 가리키는 대로 땅바닥을 바라봤지만 날이 어두워 잘 보이질 않았다. 고물상은 들르지도 못했는데 벌써 깜깜했다. 구도심은 해가 지고 나면 이미 밤이나 다름없었다. \n&nbsp;\n&nbsp;“고양이가 있어.”\n&nbsp;\n&nbsp;자세히 보니 진짜로 고양이가 있었다. 귀 끝부터 꼬리 끝까지 털이 새카매서 잘 보이지 않았던 것이다. 자기 위치를 알리기라도 하려는 듯 노란 눈을 크게 뜬 고양이가 야옹 하고 울었다. 낯도 안 가리는지 산이 손을 내밀자 얼른 와서 얼굴을 갖다 댔다. 산은 익숙하게 고양이를 쓰다듬었다. 구도심에는 동물이 별로 없어서 우영도 신기하게 고양이를 구경했다.\n&nbsp;\n&nbsp;“영아, 얘 우리가 키우면 안 돼?”\n&nbsp;“어 안 돼.”\n&nbsp;“왜애. 키우자. 응?”\n&nbsp;\n&nbsp;산이 철없이 고집을 부렸다. 안 그래도 벌이가 줄었는데 입을 하나 더 늘린다니 절대 안 될 일이었다. 우영은 그냥 고양이랑 길바닥에서 같이 살라며 으름장을 놓았다. 어느새 고양이를 안아 든 산이 입술을 삐죽거렸다.\n&nbsp;\n&nbsp;“이렇게 추운 날 밖에서 살면 얼어 죽는단 말이야.”\n&nbsp;“우리가 굶어 죽게 생겼다고.”\n&nbsp;“난 밥 안 먹어도 되니까 괜찮다고 생각해.”\n&nbsp;“너 나 죽는 꼴 보고 싶어?”\n&nbsp;\n&nbsp;그치만...... 산이 시무룩하게 고양이 등을 쓰다듬으며 말했다.\n&nbsp;\n&nbsp;“영이 너랑 닮았는 걸. 이렇게 추운데 혼자 두고 갈 수는 없어.”\n&nbsp;\n&nbsp;우영은 바보 같게도 그 말에 마음이 약해지고 말았다. 결국 고양이는 산의 품에 안겨 가서는 판잣집 한구석에 자리를 잡게 되었다. 방금 만났는데도 산이 퍽이나 마음에 든 것 같았다. 조금의 반항도 없이 안겨 오더니 방 안에 놓아준 후에도 꼬리를 세우고 산을 따라다녔다. 이 집 주인은 난데. 건방진 고양이였다.\n&nbsp;\n&nbsp;“이름은 뭘로 할까? 별이 어때, 별이?”\n&nbsp;“산이 별이 세트잖아. 치사하게.“\n&nbsp;“그럼 우산이? 우리 둘 이름에서 한 글자씩 따서.”\n&nbsp;“네 이름만 다 들어가 있는데? 치사한 놈.”\n&nbsp;“이름이 한 글잔 걸 어떡해.”\n&nbsp;\n&nbsp;산이 울상을 지었다. 결국 까만 고양이의 이름은 흔해빠진 나비로 정해졌다. 산은 그래도 좋다며 나비야, 나비야 하고 잘도 불러댔다. 이름을 불리면 나비는 소리 높여 대답을 했다. 이제 집안이 한 인간과 한 안드로이드와 한 고양이가 떠드는 소리로 시끄러워졌다. 할아버지가 살아 계실 때도 이렇게 집이 소란스러워졌던 적은 없는 것 같다. 그런데 왠지 싫지는 않았다. 인정하고 싶지 않았지만 우영은 그때 산을 팔아버리지 않아서 정말 다행이라고 생각했다.\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;/\n&nbsp;\n&nbsp;현장을 옮기기로 했다. 기존에 다니던 곳은 얼어서 발굴이 어렵기도 했지만, 이제 더 이상 파낼 만한 게 없다는 판단 하에서였다. 너무 오랫동안 한 곳에 있어서 지겹기도 했다. 다음 현장은 산에게 구도심 지도를 보여주고 정하게 했다. 어차피 걸어 다니는 거리에서 갈 수 있는 곳은 많지 않았지만.\n&nbsp;새 현장은 지금까지 다녔던 다른 현장들과 마찬가지로 핵폭발로 모든 게 무너지거나 사라진 폐허였다. 아스팔트와 흙더미를 파내서 그나마 형태가 남아있는 물건이 묻혀있는 부분에 도달하기까지 몇 주나 걸렸다. 그렇게 고생을 해서 자리를 뒤엎고 나면 그 아래 묻힌 것들이 하나둘씩 눈에 보이기 시작했다. 산과 우영은 다시 몇 주에 걸쳐 물건들을 파냈다. 어느 날은 뒤집힌 거대한 가구를 발견했다. 그 안에 뭐가 들어있을지 기대하며 한 사람과 두 안드로이드는 힘을 합쳐 가구를 뒤집었다. 그건 커다란 책장이었다. 산은 실망한 기색이었다. 하지만 우영은 기뻐하며 책을 빼냈다.\n&nbsp;\n&nbsp;“잘 됐다. 고물상에 가져가자.”\n&nbsp;“이거 비싼 거야?”\n&nbsp;“닥터가 책을 수집하거든. 책을 좋아해.”\n&nbsp;\n&nbsp;깊은 땅 밑에 묻혀있던 책들은 상태가 좋은 편이었다. 둘은 흙을 잘 털어 책을 수레에 싣고 고물상까지 날랐다. 닥터는 수레를 보더니 단번에 '이건 책이 아니라 잡지'라고 했다. 우영과 산은 그 둘의 차이를 잘 몰랐다. 다행히 닥터는 잡지도 받아줬다. 감정이 아니라 독서에 푹 빠져버린 닥터를 두고 산과 우영은 미남 사장이 갖다 준 과자를 나눠 먹었다. 그러다 문득 닥터가 잡지의 어느 페이지를 보여주며 말했다.\n&nbsp;\n&nbsp;“이것 봐봐. 산이랑 닮지 않았어?”\n&nbsp;\n&nbsp;한 인간과 한 안드로이드는 동시에 잡지로 시선을 향했다. 닥터의 손가락이 가리킨 것은 어느 남자의 사진이었다. 페이지의 4분의 1 정도의 크기로 사진은 작았다. 그 남자는 검은 머리에 검은색 정장을 입고 있었고 배경은 어느 건물 앞이었다. 카메라를 똑바로 쳐다보지 않고 상반신만 찍은 사진이었다. 그리고 얼굴은 정말로 산을 쏙 빼닮아 있었다. 굵은 목도, 턱선도, 도톰한 입술도 모두 같았다. 우영은 옆자리의 산을 봤다가, 다시 사진 속 남자를 봤다. 옷차림과 머리색 말고는 같은 사람이라고 생각될 정도로 똑같은 얼굴이었다. 이 남자의 얼굴에도 산과 같은 곳에 점이 있는지 알고 싶었지만 그 정도로 화질이 좋지는 않았다.\n&nbsp;\n&nbsp;“진짜 똑같지? 누굴까? 최......”\n&nbsp;\n&nbsp;닥터는 사진 밑에 있는 글을 읽으려고 했지만 글자 부분은 심하게 훼손되어 있어서 읽을 수가 없었다. 사진 바로 밑에는 작은 글씨로 이름이 쓰여 있는 듯 했지만 그것도 첫 글자 말고는 분간이 가질 않았다. \n&nbsp;\n&nbsp;“혹시 이 사람을 모델로 산이를 만든 건가? 뭐 하는 사람이지?”\n&nbsp;“산아, 이 사람 누군지 알아?”\n&nbsp;\n&nbsp;닥터와 우영이 질문을 쏟아냈지만 산은 여느 때와 다름없이 태연한 얼굴이었다. 그 태평한 표정을 보자 우영은 왠지 안심이었다. 늘 하던 대로 모른다고 할 것만 같아서. 하지만 산의 입에서는 뜻밖의 말이 흘러나왔다.\n&nbsp;\n&nbsp;“난...... 아마 이 사람을 위해 태어난 것 같아.”\n&nbsp;\n&nbsp;그 말이 왜 그렇게까지 충격이었는지 모르겠다. 우영은 산이 망치로 뒤통수를 때리기라도 한 듯 멍해졌다. 무슨 뜻이냐고 채근하자 산은 모르겠다고만 했다. 그럼 방금 했던 말은 뭔데? 이 사람은 누구야? 너랑 무슨 관계야? \n&nbsp;\n&nbsp;“그냥 그런 생각이 들었어.”\n&nbsp;\n&nbsp;산은 별것도 아니라는 투였다. 사실 그럴지도 모른다. 닥터는 그 사람이 K사의 고위직이고, 산은 이 사람을 모델로 만든 프로토타입 안드로이드고, 그래서 그 사람이 산을 소유했었을 거라고 추측했다. 그렇다면 둘의 얼굴이 똑 닮은 것도 어쩔 수 없는 일이었다. 그 사람이 산의 주인이었다면 산은 그를 위해 만들어진 게 맞겠지. 목적 없이 만들어지는 안드로이드는 없으니까. 머리로는 알겠는데, 우영은 이유 모를 기묘한 답답함에 시달려야 했다.\n&nbsp;감정실을 나오고 나서도 내내 정신이 차려지질 않았다. 산이 말을 해도 듣는 둥 마는 둥 했고 저녁도 대충 먹었다. 오늘은 일찍 자자며 침대에 누워서는 오히려 정신이 또렷해졌다. 옆에 누운 산이 낯설게 느껴졌다. 산에게 지구 멸망 이전의 삶이 있었던 건 당연한 건데도 지금까지 생각해본 적이 없었다. 산은 아무것도 몰랐으니까. 마치 예전에는 그저 잠들어 있었고 진정 태어난 것은 우영과 만난 순간부터였다는 것처럼 생각했었다. 아기새가 알을 깨고 나오면 처음 보는 생물을 부모처럼 생각하듯이, 산이라는 안드로이드에게는 정우영이 전부라고 무의식적으로 믿어버렸다. 그러나 사실은 산에게도 분명히 과거가 있고, 그 과거에는 얼굴만 봐도 '이 사람을 위해 태어났다'고 느낄 정도로 강렬한 감정을 가진 사람도 있었다. 그 사실이 우영에게는 너무나 충격적이고, 너무나 이상했다.\n&nbsp;도저히 잠이 오지 않았다. 살짝 고개를 돌려 옆을 보자 산의 눈 감은 얼굴이 보였다. 눈앞에 손바닥을 저어 봤지만 반응은 없었다. 우영은 조심스레 일어나서 침대를 빠져나왔다. 현관의 신발장 겸 잡동사니 창고의 제일 밑 칸에 놓인 금속 상자를 열면 그 안엔 담배와 라이터가 있었다. 할아버지 유품이었다. 우영은 잘 피우지 않았지만 지금은 무척 담배가 말렸다. 소리가 나지 않게 조심해서 담뱃갑과 라이터를 꺼낸 우영은 집 밖으로 나갔다.\n&nbsp;구도심의 밤은 칠흑처럼 까맣다. 신도심은 밤에도 번쩍번쩍 화려하다고 하는데, 구도심은 해가 저물면 빛이라고는 찾아볼 수 없다. 그런 어둠 속에서 우영이 담뱃불을 붙이려 켠 라이터 불만이 홀로 빛났다. 우영은 집 앞에 쪼그려 앉아 담배를 피웠다. 왜 이렇게 가슴이 답답한지 스스로도 알 수 없었다. 체한 것처럼 속에 뭔가 걸린 것 같았다. 울면서 소리 지르고 싶기도 했다. 왜? 그걸 알 수 없어서 기분이 나빴다.\n&nbsp;\n&nbsp;“우영아.”\n&nbsp;“악!”\n&nbsp;\n&nbsp;갑작스레 말을 거는 목소리에 깜짝 놀란 우영이 소리를 질렀다. 밤늦은 시각인 걸 깨달은 우영이 한 손으로 입을 막았다. 목소리의 주인공은 산이었다. 잠옷 차림으로 외투도 걸치지 않은 채 나온 산은 무척이나 추워 보였지만 전혀 춥지 않은 것 같았다. \n&nbsp;\n&nbsp;“뭐해?”\n&nbsp;“그냥...... 잠이 안 와서.”\n&nbsp;\n&nbsp;우영은 다시 쪼그려 앉은 채 연기를 내뿜었다. 얼굴을 숙이자 빨갛게 언 발가락이 보였다. 추웠다.\n&nbsp;\n&nbsp;“없어져서 걱정했잖아.”\n&nbsp;\n&nbsp;산도 우영의 옆에 쪼그리고 앉았다. 아무렇게나 꿰차고 나온 신발은 슬리퍼여서 산의 맨발이 보였다. 이 추위에도 그저 하얀 발은 추위를 전혀 느끼지 못하는 것 같았다.\n&nbsp;\n&nbsp;“너 담배 피웠어?”\n&nbsp;“가끔.”\n&nbsp;“피우지 마. 몸에 안 좋잖아.”\n&nbsp;\n&nbsp;이렇게 우영의 건강을 걱정해주기도 하는데 말이다. 다 인공지능에 입력된 대로 하는 말일까? 진심으로 우영을 생각해주는 듯한 저 눈빛은 뭐고. 모든 게 그냥 만들어진 것이란 게 도무지 믿어지질 않았다. 우영은 말없이 담배를 태웠다. 장초는 금세 짧아졌다. 할아버지의 유품이라 너무 금방 없애버리고 싶지 않아서 한 번에 한 개비까지만으로 정해둔 탓에 늦은 밤의 일탈은 그걸로 끝이었다. 우영은 짧아진 담배를 바닥에 비벼 끄려고 했다. 그 순간, 마찬가지로 말없이 지켜보고 있던 산이 손을 내밀었다.\n&nbsp;\n&nbsp;“자.”\n&nbsp;“......뭐야?”\n&nbsp;\n&nbsp;영문을 몰라 하는 우영의 손목을 잡고 그대로 자기 손바닥에 담뱃불을 갖다 댄다. 질겁한 우영이 팔을 뿌리치려 했지만 너무 순식간이었다. 손바닥에 꽁초가 닿는 느낌이 닿자 우영은 한밤중이라는 것도 잊고 소리를 질렀다.\n&nbsp;\n&nbsp;“야! 너 손바닥에 구멍 내려고 그래?”\n&nbsp;“짠.”\n&nbsp;\n&nbsp;산이 해맑게 웃으며 손바닥을 펼쳐 보였다. 재가 묻긴 했지만 멀쩡했다. 재를 털어버리자 평소와 다름없이 깨끗한 손이 되었다. 그러나 우영의 손에 들린 담배는 불이 완전히 연소된 꽁초로 변해 있었다. 우영은 얼이 빠져 아무 말도 하지 못했다.\n&nbsp;\n&nbsp;“난 구멍 같은 거 안 나. 이제 들어가자. 춥다.”\n&nbsp;\n&nbsp;그렇게 말하는 산은 전혀 추운 투가 아니었다. 우영은 여전히 정신을 차리지 못하고 산이 이끄는 대로 집 안에 들어갔다. 다시 침대에 나란히 눕고도 우영은 심란해서 잠이 들지 못했다. 조금만 고개를 틀어 눈 감은 산의 얼굴을 바라보면 처음 만난 그날과 똑같은 모습이었다. 우영은 이마를 지나 눈썹, 눈꺼풀, 콧날, 턱을 차례대로 훑어보았다. 목과 쇄골, 이불을 들어내고 그 밑의 가슴과 배도 관찰했다. 어디를 봐도 그저 얌전히 잠든 사람인데, 우영은 곁의 남자가 사실은 모종의 재료로 누군가가 만들어낸 존재라는 것이 이상했다. 근본적으로는 지금 누워있는 침대와 같고, 더 나아가봐야 밥솥이나 냉장고와 별다를 게 없다는 것이. 산이 우영에게 자길 팔지 말아 달라 하고, 추운 밤에 나가지 말라고 하고, 건강에 안 좋으니까 담배 피우지 말라고 하던 게 본능이나 어떤 이끌림 때문이 아니라 인공지능일 뿐일지도 모른다는 생각이 우영을 좀처럼 잠 못 들게 했다.\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;\n&nbsp;/\n&nbsp;\n&nbsp;잠을 설쳤는데도 아침에는 일어나야 했고 일을 하러 나가야 했다. 오늘은 나가지 말자고 할까도 생각했지만 그러면 좁은 집에서 계속 둘이 있어야 하니 그것도 싫었다. 차라리 힘들게 일해서 머리를 비우는 게 나을 것 같았다. 산은 얄미울 정도로 평소와 똑같았다. 아무 일도 없었는데 웃으면서 나비와 놀아주고 아침 먹는 우영을 기다렸다. 미소 띤 산의 뺨에는 보조개가 패었다. 우영은 그 보조개를 손가락 끝으로 푹 찔러보고 싶은 충동을 느꼈다. 그냥 만져보고 싶기도 했고 산의 울상을 보게 장난을 치고 싶기도 했다. 그런데 보조개도 울상도 누군가가 만든 것이라고 생각하면 다 때려치우고 그 만든 새끼를 한 대 때려주고 싶어졌다.\n&nbsp;우영의 속도 모르고 하늘이 아주 청명했다. 날이 좋아서 산은 기분이 좋은 듯했다. 현장으로 가는 내내 콧노래를 부르고 난리도 아니었다. 날씨가 맑은 덕에 가는 길이 편하긴 했다. 둘은 수레를 끌고 걷고 또 걸어서 현장에 도착하자마자 작업에 착수했다. 파두었던 곳을 계속해서 파내다 보니 뻥 뚫린 통로가 나왔다. 조금 더 들어가자 계단이었다. 우영과 산은 서로의 얼굴을 쳐다보았다.\n&nbsp;\n&nbsp;“내려가 볼까?”\n&nbsp;“조심해.”\n&nbsp;\n&nbsp;어두워서 손전등을 켜고 천천히 걸어 내려가야 했다. 계단 아래는 신기한 공간이었다. 지하라서 그런지 보존 상태가 아주 좋았다. 난생처음 보는 기계도 있었고 커다란 옛날 광고판도 있었다. 우영은 입을 헤 벌리고 두리번거리며 걸었다. 이런 광경은 신도심에서나 볼 수 있을 법한 것들이었다. 바닥에는 아마도 핵폭발에서는 살아남았지만 결국엔 굶어 죽은 사람들의 유골이 나뒹굴고 있었다. 우영과 산은 시체를 넘어 계속 걸었다.\n&nbsp;\n&nbsp;“여기가 종로3가래.”\n&nbsp;“그게 뭐야?”\n&nbsp;“나도 몰라.”\n&nbsp;\n&nbsp;내려가고 또 내려가서 걸어가고 또 걸어가다 보니 끝없이 이어지는 철로가 놓인 길이 나타났다. 이제 한 치 앞도 잘 보이지 않는 어둠이었다. 우영은 겁이 나서 전등을 잡지 않은 손으로 산의 팔을 붙잡았다. 철로를 따라 걷다 보면 일정한 간격으로 간판이 나왔다. 크게 '종로3가'라고 적힌 글자 밑에 '종로5가'와 '종각'이라고 작게 적힌 것을 계속 보다 보니 그게 역명이라는 감이 왔다. 우영은 크게 흥분했다. 우리가 유적지를 발견했어. 말로만 듣던 지하철이었다. 역을 통째로 고물상에 들고 갈 수는 없지만 가져갈 수 있는 것은 다 가져가고 싶었다.\n&nbsp;\n&nbsp;“우영아!”\n&nbsp;\n&nbsp;한껏 들떠 멈추지 않던 우영이 벽에 부딪힐 뻔하자 산이 어깨를 감싸 안으며 막았다. 손전등으로 비춰보니 토사가 굳어 길이 반쯤 막혀 있었다. 뚫린 곳으로 몸을 구겨 넣으면 지나갈 수 있었지만 왔던 길과는 달리 길이 무너진 흔적이 역력했다.\n&nbsp;\n&nbsp;“위험해 보인다. 그냥 돌아가자.”\n&nbsp;“많이 걸어와서 조금만 걸어가면 다음 역 나와.”\n&nbsp;“어차피 짐 두고 와서 다시 올라가야 되잖아.”\n&nbsp;“다음 작업하려면 어디까지 갈 수 있는지 알아두는 게 좋단 말이야.”\n&nbsp;\n&nbsp;우영이 고집을 부렸다. 결국엔 산도 두 손 두 발 다 들고 말았다. 대신 정말 조심해야 한다는 조건을 내걸고, 둘은 철로를 따라 어두운 통로를 걷기 시작했다. 오랫동안 밀폐되어 있던 지하 통로는 두 남자가 걸으면서 내는 진동에도 민감한지 걸을 때마다 천장이며 벽이 후두둑 떨어졌다. 겁이 나면서도 들뜬 우영은 산의 한쪽 팔을 꽉 붙잡은 채로 움직였다. 미지의 공간을 탐험하는 어린아이처럼 불안과 흥분이 혼재했다.\n&nbsp;조심하며 천천히 걸었는데도 너무 어두워서 앞이 잘 보이지 않는 탓에 우영이 발을 헛디뎌 넘어졌다. 넘어지며 벽에 부딪힐 뻔한 것을 산이 재빨리 몸을 감싸며 대신 부딪혔다. 그 충격에 벽과 천장이 일부 무너지고 말았다. 우영은 외마디 비명을 질렀다. 부리나케 달려들어 넘어진 산을 일으켰다.\n&nbsp;“산아, 너......!”\n&nbsp;“우영아, 괜찮아?”\n&nbsp;“너...... 피 나.”\n&nbsp;\n&nbsp;산의 오른쪽 눈썹 바로 밑이 몇 센티미터쯤 찢어져 피가 흐르고 있었다. 상처는 크지 않았는데도 연약한 부위라 그런지 피가 많이 흘렀다. 우영은 너무 놀라서 눈물이 쏟아졌다.\n&nbsp;\n&nbsp;“난 괜찮아.”\n&nbsp;\n&nbsp;피 때문에 한쪽 눈을 뜨지 못하는 산은 마치 윙크하는 꼴이었는데도 우영은 웃지 못했다. 눈물이 주룩주룩 흘러 온통 얼굴을 적셨다. 가방에 넣어놓은 응급처치 도구를 꺼내려는데 손이 덜덜 떨려서 몇 번이고 헛손질을 했다. 간신히 가방을 열었어도 좀처럼 약과 반창고를 꺼내지 못하는 우영의 손을 산이 잡았다.\n&nbsp;\n&nbsp;“우영아, 그만 울어. 나 괜찮아.”\n&nbsp;“피가 그렇게 나는데 뭐가 괜찮아......”\n&nbsp;“내가 피가 난다고? 이상하네.”\n&nbsp;\n&nbsp;산은 다른 한쪽 눈도 접어가며 웃었다. 우영은 퉁퉁 붓고 젖은 얼굴로 산을 쳐다봤다. 펑펑 울 정도로 놀라서 경황이 없었지만 생각해보니 이상했다. 겉모습만 사람이지 속은 기계인 산이 왜 피를 흘리는 건지. 너무나 정교하게 만들어서 가짜 피가 나는 거라고 하기엔, 손바닥은 담뱃불을 지져도 멀쩡했다. 우영은 혼란스러워서 토할 것 같았다. 도대체 인간인지 로봇인지 정체를 모르겠다. 놀란 우영을 안심시키려고 그러는지 산은 계속 웃고 있었다. 피 때문에 오른쪽 눈을 못 뜨니 윙크하는 것처럼 감고 뺨에는 깊게 보조개가 파였다.\n&nbsp;이럴 때가 아닌데 우영은 역시 산의 웃는 얼굴이 너무 좋았다. 빨리 피를 닦고 약을 발라줘야 하는데 마냥 바라보기만 했다. 좀 제정신이 아닌 것 같았다. 어두운 지하 통로에서 손전등 두 개의 빛에만 의존해 봐야 하는 얼굴은 그 어느 때보다 우영의 심장을 빨리 뛰게 만들었다. \n&nbsp;우영은 충동적으로 산의 입술에 입을 맞췄다 바로 뗐다. 다른 것은 생각할 겨를도 없었다. 빨리 여기를 나가야 한다든지, 어서 치료를 해줘야 한다든지, 갑자기 내가 왜 이러는지. 그런 것보다도 그냥 입을 맞추고 싶었다. 찰나의 순간 동안 맞부딪힌 입술은 부드러웠다. 난생처음 느껴본 누군가의 입술은 너무 말랑해서 아무래도 인공적으로 만들어진 게 맞다는 생각이 들었다.\n&nbsp;\n&nbsp;“미안.”\n&nbsp;\n&nbsp;우영은 서둘러 사과하고는 가방에서 약과 반창고를 꺼냈다. 이제는 아까만큼 손이 떨리지 않았다. 우영은 피를 닦아내고 약을 발랐다. 그동안 산은 말없이 우영을 내려다보고 있었다. 그 침묵이 신경 쓰였지만 애써 침착한 척했다. 심장이 가늘게 떨리는 것 같았다. 차마 눈을 마주칠 수가 없어 아래만 보고 반창고를 떼어 내는데 산이 우영의 손을 잡았다. 무심코 고개를 들자 아까 우영이 그랬던 것처럼 짧게 입을 맞춰 온다.\n&nbsp;\n&nbsp;“......뭐야?”\n&nbsp;“뽀뽀.”\n&nbsp;“왜......?”\n&nbsp;“우영이 넌 아까 왜 그랬는데?”\n&nbsp;“난... 그냥 한 거야.”\n&nbsp;“나도 그냥 한 거야. 하고 싶어서.”\n&nbsp;\n&nbsp;어두운 곳에서 마주 본 두 눈은 마치 심연과 닮아 있었다. 우영은 머리가 어지러웠다. 산은 대체 뭘까. 안드로이드면서 우영을 걱정하고, 자기가 하고 싶으면 키스도 한다. 그렇지만 이 몸은 분명히 철, 배터리, 전선, 실리콘 같은 것들로 이루어져 있다. 피로를 모르고 담뱃불로 지져도 아무렇지도 않다. 그렇지만 다치면 피를 흘린다. 그리고 무엇보다도 우영을 이런 눈으로 쳐다본다.\n&nbsp;우영은 아까와는 다른 이유로 울고 싶어졌다. 이 눈빛이 가짜라면 죽는 게 나을 것 같았다."
